<template>
  <div class="hello">
    <canvas :id="green" ref="canvas" :width="width" :height="height" @mousedown="mousedown($event)" @mousemove="mousemove($event)" @mouseup="mouseup($event)" @touchstart="touchstart($event)" @touchmove="touchmove($event)" @touchend="touchend"></canvas>
    <change class="color" @change="change" @changeback="changeback" @changeweight="changeweight" @eraser="eraser" @clear="clear" @save="save"></change>
  </div>
</template>

<script>
import Change from './change'
export default {
  name: 'Canvas',
  data () {
    return {
      width:'',
      height:'',
      isMouse:false,
      can:'',
      ctx:'',
      lastposi:{x:undefined,y:undefined},
      newposi:{x:undefined,y:undefined},
      x:'',
      x1:'',
      y:'',
      y1:'',
      color:'red',
      lineWidth:6,
      green:'green',
      isEraser:false,
      istouch:false,
    }
  },
  components:{
    Change
  },
  methods:{
    touchstart($event){
      this.x = $event.touches[0].clientX
      this.y = $event.touches[0].clientY   
      this.isMouse = true
      this.startrender()
    },
    touchmove($event){
      this.x1 = $event.touches[0].clientX
      this.y1 = $event.touches[0].clientY  
      this.render()
    },
    touchend(){
      this.isMouse=false
    },
    mousedown($event){
      this.x = $event.clientX
      this.y = $event.clientY   
      this.isMouse = true
      this.startrender()
    },
    mousemove($event){
      this.x1 = $event.clientX
      this.y1 = $event.clientY  
      this.render()
    },
    mouseup(){
      this.isMouse=false
    },
    line(x,y,x1,y1){
        this.ctx.beginPath()
        this.ctx.moveTo(x,y);
        this.ctx.lineWidth=this.lineWidth
        this.ctx.strokeStyle = this.color
        this.ctx.lineTo(x1,y1);
        this.ctx.closePath()
        this.ctx.stroke()
    },
        startrender(){
      this.can =this.$refs.canvas
      this.ctx=this.can.getContext('2d')
      if(this.isEraser){
        this.ctx.fillStyle = this.green
        this.ctx.fillRect(this.x-this.lineWidth/2,this.y-this.lineWidth/2, this.lineWidth, this.lineWidth);
      }else{
        this.lastposi={'x':this.x,'y':this.y}        
      }
    },
    render(){
      if(!this.isMouse)return
      if(this.isEraser){
        this.ctx.fillStyle = this.green
        this.ctx.fillRect(this.x1-this.lineWidth/2,this.y1-this.lineWidth/2, this.lineWidth, this.lineWidth);
      }else{
        this.newposi ={'x':this.x1,'y':this.y1} 
        this.line(this.lastposi.x,this.lastposi.y,this.newposi.x,this.newposi.y)                    
        this.lastposi =this.newposi
      }
    },
    change(value){
      this.color=value
    },
    changeback(back){
      this.green=back
      this.ctx.fillStyle = back
      this.ctx.fillRect(0, 0,parseInt(this.width),parseInt(this.height))
    },
    changeweight(weight){
      switch(weight){
        case 'small':this.lineWidth = 3;break;
        case 'medium':this.lineWidth = 6;break;
        case 'big':this.lineWidth = 11;break;
        case 'bigplus':this.lineWidth = 16;break;
        default:break;
      }return
    },
    eraser(eraser){
      this.isEraser=eraser
    },
    clear(isclear){
      if(isclear){
        this.ctx.clearRect(0, 0,parseInt(this.width),parseInt(this.height));
      }
    },
    save(href){
      if(href){
        var url = this.can.toDataURL({
          format: 'png',
          multiplier: 4
        })
        var a =document.createElement('a')
        document.body.appendChild(a)
        a.href=url
        a.download='myproject'
        a.click()
      }
    },
  },
  mounted(){
    this.can =this.$refs.canvas
    this.ctx=this.can.getContext('2d')
    this.ctx.fillStyle = this.green
    this.ctx.fillRect(0, 0,parseInt(this.width),parseInt(this.height))
  },
  beforeMount(){
    this.width = `${document.documentElement.clientWidth}px`;
    this.height =`${document.documentElement.clientHeight}px`;
    const that = this;
    window.onresize = function temp() {
      that.height = `${document.documentElement.clientHeight}px`;
      that.width = `${document.documentElement.clientWidth}px`;
    }
  },
}
</script>

<style scoped>
    .color{
      position: absolute;
      top:0;
      left:0;
    }
    canvas{
      position: fixed;
    }
    body{
      position: relative;
      overflow: hidden;
    }
</style>



