<template>
    <div>
        <ul>
            <li class="back"><span>画布颜色</span><button v-for="back in backgrounds" :style="{backgroundColor:back}" @click="changeback(back,$event)"></button></li>
            <li class="color"><span>画笔颜色</span><button v-for="color in colors" :style="{backgroundColor:color}" @click="changecolor(color,$event)"></button></li>
            <li class="weight"><span>粗细</span><button v-for="weight in weights" @click="changeweight(weight)"><div :class="weight"></div></button></li>
            <li class="eraser" @click="eraser" :class="icon">{{this.text}}</li>
            <li class="icon-clear" @click="clear"></li>
            <li class="icon-save" @click="save"></li>
        </ul>
    </div>
</template>
<script>
import $ from 'jquery'
export default {
    name:'Change',
    data(){
        return{
            colors:['red','black','blue','white','yellow','green','pink','purple','orange','Lime'],
            backgrounds:['red','black','blue','white','yellow','green','pink','purple','orange','Lime'],
            weights:['small','medium','big','bigplus'],
            isEraser:false,
            text:'画笔',
            border:'3px solid red',
            icon:'icon-pen',
            isclear:true,
            href:true
        }
    },
    methods:{
        changecolor(color,$event){
            $('button').each(function(){
                this.style.border='none'
            })
            $($event)[0].path[0].style.border = this.border
            this.$emit('change',color)
        },
        changeback(back,$event){
            $('button').each(function(){
                this.style.border='none'
            })
            $($event)[0].path[0].style.border = this.border
            this.$emit('changeback',back)
        },
        changeweight(weight){
            this.$emit('changeweight',weight)
        },
        eraser(){
            if($('.eraser')[0].innerText =='画笔'){
                $('.eraser')[0].innerText='橡皮擦'
                this.icon='icon-eraser'
                this.isEraser = true
            }else{
                $('.eraser')[0].innerText='画笔'
                this.isEraser=false
            }
            this.$emit('eraser',this.isEraser)
        },
        clear(){
            //console.log('12')
            this.$emit('clear',this.isclear)
        },
        save(){
            this.$emit('save',this.href)

        }        
    }
}
</script>
<style scoped>
@import url('//at.alicdn.com/t/font_1116506_o10o9jqqw4.css');
 button{
    width: 3vh;
    height:3vh;
    border:1px solid #666;
 }
 li{
    width: 6vh;
    border:3px solid #ccc;
    list-style: none;
    background-color: white;
    text-align: center;
    font-weight: bold;
    font-size:3vh;
    color: #444;
 }
 span{
    text-align: center;
    display:inline-block;
    width: 6vh;
    height: 6vh; 
 }
 .small{
     width: 1vh;
     height: 1vh;
 }
 .medium{
     width: 1.5vh;
     height: 1.5vh;
 }
 .big{
     width: 2vh;
     height: 2vh;
 }
 .bigplus{
     width: 2.5vh;
     height: 2.5vh;
 }
 button div{
     margin: 0 auto;
     background-color:black!important
 }
 .eraser{
     height: 12vh;
     background-color: pink;
     font-family: 'iconfont'
 }
 .weight span{
     height: 3vh;
 }
 .icon-clear,.icon-save{
     font-family: 'iconfont'
 }
</style>




